.Home_main__VkIEL {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 6rem;
  min-height: 100vh;
}

.Home_description__uXNdx {
  display: inherit;
  justify-content: inherit;
  align-items: inherit;
  font-size: 0.85rem;
  max-width: var(--max-width);
  width: 100%;
  z-index: 2;
  font-family: var(--font-mono);
}

.Home_description__uXNdx a {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.Home_description__uXNdx p {
  position: relative;
  margin: 0;
  padding: 1rem;
  background-color: rgba(var(--callout-rgb), 0.5);
  border: 1px solid rgba(var(--callout-border-rgb), 0.3);
  border-radius: var(--border-radius);
}

.Home_code__VVrIr {
  font-weight: 700;
  font-family: var(--font-mono);
}

.Home_grid__AVljO {
  display: grid;
  grid-template-columns: repeat(4, minmax(25%, auto));
  width: var(--max-width);
  max-width: 100%;
}

.Home_card__E5spL {
  padding: 1rem 1.2rem;
  border-radius: var(--border-radius);
  background: rgba(var(--card-rgb), 0);
  border: 1px solid rgba(var(--card-border-rgb), 0);
  transition: background 200ms, border 200ms;
}

.Home_card__E5spL span {
  display: inline-block;
  transition: transform 200ms;
}

.Home_card__E5spL h2 {
  font-weight: 600;
  margin-bottom: 0.7rem;
}

.Home_card__E5spL p {
  margin: 0;
  opacity: 0.6;
  font-size: 0.9rem;
  line-height: 1.5;
  max-width: 30ch;
}

.Home_center__O_TIN {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 4rem 0;
}

.Home_center__O_TIN::before {
  background: var(--secondary-glow);
  border-radius: 50%;
  width: 480px;
  height: 360px;
  margin-left: -400px;
}

.Home_center__O_TIN::after {
  background: var(--primary-glow);
  width: 240px;
  height: 180px;
  z-index: -1;
}

.Home_center__O_TIN::before,
.Home_center__O_TIN::after {
  content: '';
  left: 50%;
  position: absolute;
  filter: blur(45px);
  transform: translateZ(0);
}

.Home_logo__IOQAX {
  position: relative;
}
/* Enable hover only on non-touch devices */
@media (hover: hover) and (pointer: fine) {
  .Home_card__E5spL:hover {
    background: rgba(var(--card-rgb), 0.1);
    border: 1px solid rgba(var(--card-border-rgb), 0.15);
  }

  .Home_card__E5spL:hover span {
    transform: translateX(4px);
  }
}

@media (prefers-reduced-motion) {
  .Home_card__E5spL:hover span {
    transform: none;
  }
}

/* Mobile */
@media (max-width: 700px) {
  .Home_content__tkQPU {
    padding: 4rem;
  }

  .Home_grid__AVljO {
    grid-template-columns: 1fr;
    margin-bottom: 120px;
    max-width: 320px;
    text-align: center;
  }

  .Home_card__E5spL {
    padding: 1rem 2.5rem;
  }

  .Home_card__E5spL h2 {
    margin-bottom: 0.5rem;
  }

  .Home_center__O_TIN {
    padding: 8rem 0 6rem;
  }

  .Home_center__O_TIN::before {
    transform: none;
    height: 300px;
  }

  .Home_description__uXNdx {
    font-size: 0.8rem;
  }

  .Home_description__uXNdx a {
    padding: 1rem;
  }

  .Home_description__uXNdx p,
  .Home_description__uXNdx div {
    display: flex;
    justify-content: center;
    position: fixed;
    width: 100%;
  }

  .Home_description__uXNdx p {
    align-items: center;
    inset: 0 0 auto;
    padding: 2rem 1rem 1.4rem;
    border-radius: 0;
    border: none;
    border-bottom: 1px solid rgba(var(--callout-border-rgb), 0.25);
    background: linear-gradient(
      to bottom,
      rgba(var(--background-start-rgb), 1),
      rgba(var(--callout-rgb), 0.5)
    );
    background-clip: padding-box;
    -webkit-backdrop-filter: blur(24px);
            backdrop-filter: blur(24px);
  }

  .Home_description__uXNdx div {
    align-items: flex-end;
    pointer-events: none;
    inset: auto 0 0;
    padding: 2rem;
    height: 200px;
    background: linear-gradient(
      to bottom,
      transparent 0%,
      rgb(var(--background-end-rgb)) 40%
    );
    z-index: 1;
  }
}

/* Tablet and Smaller Desktop */
@media (min-width: 701px) and (max-width: 1120px) {
  .Home_grid__AVljO {
    grid-template-columns: repeat(2, 50%);
  }
}

@media (prefers-color-scheme: dark) {
  .Home_vercelLogo___BVuc {
    filter: invert(1);
  }

  .Home_logo__IOQAX {
    filter: invert(1) drop-shadow(0 0 0.3rem #ffffff70);
  }
}

@keyframes Home_rotate__c5eru {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}

